/* AuthForm */
.container {
  .authInput {
    max-width: 320px;
    width: 100%;
    padding: 10px;
    border-radius: 30px;
    background-color: rgba(255, 255, 255, 1);
    margin-bottom: 10px;
    font-size: 12px;
    color: black;
  &.authSubmit {
    text-align: center;
    background: #04aaff;
    color: white;
    margin-top: 10;
    cursor: pointer;
   }
  }
  .authError {
    color: tomato;
    text-align: center;
    font-weight: 500;
    font-size: 12px;
  }
}

.authSwitch {
  display: block;
  margin-top: 10px;
  margin-bottom: 50px;
  font-size: 12px;
  text-decoration: underline;
  color: #04aaff;
  cursor: pointer;
}





